export default {
	data() {
		return {
			phone:"",
			code:"",
			codeTime:0,
		}
	},
	computed: {
		disabled() {
			return this.phone === '' || this.code === ''
		}
	},
	methods: {
		check(){
			var rule = /^1[34578]\d{9}$/; 
			if (!rule.test(this.phone)) {
				uni.showToast({
					title:"手机号格式不正确",
					icon:"none"
				})
				return false;
			}
			return true
		},
		submit(){
			if (!this.check()) {
				return;
			}
			this.$H.post('/user/bindphone',{
				phone:this.phone,
				code:this.code
			},{
				token:true
			}).then(res=>{
				this.$store.commit('editUserInfo',{
					key:'phone',
					value:this.phone
				})
				uni.navigateBack({
					delta: 1
				});
				uni.showToast({
					title: '绑定成功',
					icon: 'none'
				});
			})
		},
		// 获取验证码
		getCode(){
			// 防止重复获取
			if (this.codeTime > 0) {
				return;
			}
			// 验证手机号
			if (!this.check()) return;
			// 请求数据
			this.$H.post('/user/sendcode',{
				phone:this.phone
			},{
				native:true
			}).then(res=>{
				uni.showToast({
					title: res.data.msg,
					icon: 'none'
				});
				// 倒计时
				this.codeTime = 60
				let timer = setInterval(()=>{
					if (this.codeTime >= 1) {
						this.codeTime--
					} else {
						this.codeTime = 0
						clearInterval(timer)
					}
				},1000)
			})
		},
	}
}
